.work{
    padding-top: 100px;
    background-color: #f8fcfe;
    height: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin-bottom: 100px;
}
.work_content{
    text-align: center;
}
.margin_top-none{
    margin-top: 0;
}
.work_tabs{
    text-align: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-bottom: 70px;
}
.workImg_list-all{
    padding-left: 2px;
}
.work_item{
    border: 1px solid #ebebeb;
    background-color: #fff;
}

.work_item:nth-child(even){
    border-right: none;
    border-left: none;
}
.work_link{
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
    color: #717171;
    display: block;
    margin: 25px 20px;
}
.work_item.active{
    border-top:2px solid #18cfab;
    margin-top: -2px;
    -webkit-box-shadow:1px 1px 0px #ebebeb inset;
    box-shadow:0px 1px 0px #ebebeb inset;
}

.work_item.active .work_link{
    color: #18cfab;
}
.workImg_list-item{
    width: 285px;
    height: 207px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin-left: -2px;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-perspective: 1000px;
    perspective: 1000px;
}
.workImg_list-item:hover .rotate {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
}
.rotate{
    position: relative;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
}
.workImg_list-item.hide {
    display: none;
}
.work_img{
   position: absolute;
    top: 0;
    left: 0;

}
.work_img_searchItem{
    border-top: 2px solid #18cfab;
    -webkit-box-shadow: 1px 1px 1px #18cfab inset;
    box-shadow: 0px 1px 0 rgba(24, 207, 171, 0.52) inset;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 40px 0;
    width: 285px;
    height: 207px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin-left: -1px;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background-color: #f8fcfe;
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);

}
.work-search{
    width: 40px;
    height: 40px;
    display: block;
    background-color: #fff;
    border-radius: 50%;
    margin-right: 5px;
    border: 1px solid #18cfab;
    text-align: center;
    line-height: 2.6;
}
.search_row{
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 35px;
}
.white{
    color: #fff;
    background-color:#18cfab;
}
.work_title{
    color: #18cfab;
    font-size: 24px;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    margin-bottom:14px;
}
.work_subtitle{
    font-size: 12px;
    color: #8a8a8a;
    font-family: 'Montserrat', sans-serif;
}
.work_btn{
    display: inline-block;
    background-color:#18cfab;
    margin-top: 70px;
    margin-bottom: 100px;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    padding: 22px;
    text-decoration: none;
    border-radius: 3px;
}
.work_btn.hide{
    display: none;
}
.loader_box{
    display: none;
    height: 200px;
    position: relative;
}
.loader_box.active{
    display: block;
}