.news_item{
    width: 265px;
    margin-right: 27px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin-bottom:46px;
    position: relative;
}
.news_img_box{
    text-decoration: none;
}
.news_img_box:hover .date_item{
    background-color: #18cfab;
}
.news_img_box:hover .hews_title{
    color: #18cfab;
}

.news_item:nth-child(4n){
    margin-right: 0;
}
.hews_title{
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    color: #717171;
    margin-bottom: 14px;
}
.news_title-box{
    border: 1px solid #ebebeb;
    padding: 13px 11px;
    background-color: #f8fcfe;
}
.admin{
    font-size: 14px;
    font-family: 'Montserrat', sans-serif;
    color: #777777;
}
.coments{
    font-size: 14px;
    font-family: 'Montserrat', sans-serif;
    color: #777777;
}
.admin:after{
    content: "";
    height: 14px;
    width: 1px;
    background-color: #777;
    display: inline-block;
    margin-left: 14px;
    margin-right: 14px;
    margin-bottom: -2px;
}
.date_item{
    width: 68px;
    height: 68px;
    background-color: #203e38;
    display: block;
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    padding-top: 12px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    right: 0;
}
.date{
    width: 100%;
    display: block;
    margin-bottom: 12px;
}