.options{
    margin-bottom: 100px;
}
.options_box{
    width: 25%;
    background: #18cfab;
    border-top: 7px solid #fff;
    padding: 125px 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: #fff;
}
.options_box:nth-child(odd){
    background-color:#1c2a33;
    border-top: 7px solid #18cfab;
    color: #18cfab;
    line-height: 1.5;
}
.options_box-img{
    height: 110px;
/*    display: block;*/
    width: 100px;
    margin-bottom:20px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.options_subtitle{
    width: 100%;
    text-align: center;
    font-size:20px;
     font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
}
