.header{
    background-color: rgba(96, 95, 90, 0.61);
    min-width: 100%;
/*    min-height: 100%;*/
     padding: 30px 0;
}

.title_link{
    text-decoration: none;
    position: relative;
}
.title_logo{
        background-color: #18cfab;
        width: 60px;
        height: 35px;
        display: inline-block;
        text-transform: uppercase;
        font-size: 40px;
        text-align: center;
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
        color: #fff;
        line-height: 0.9;
        margin-right: 8px;
}
.title_logo:before{
    content: "";
    width: 0;
    height: 0;
    border: 30px solid transparent;
    border-bottom: 15px solid #18cfab;
    display: inline-block;
    position: absolute;
    left: 0;
    bottom: 35px;
}

.title_logo:after{
    content: "";
    width: 0;
    height: 0;
    border: 30px solid transparent;
    border-top: 15px solid #18cfab;
    display: inline-block;
    margin-top: -1px;
}

.this{
    font-size: 32px;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
}
.ham{
    font-size: 32px;
    color: #18cfab;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
}
.header_item{
    margin-right:25px;
    position: relative;
}
.header_link{
    text-decoration: none;
    text-transform: uppercase;
    font-size: 13px;
    color: #b8b8b8;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    display: inline-block;
}
.header_link-search{
    font-size: 13px;
    color: #b8b8b8;
    display: inline-block;
}
.header_link-search:hover{
    color: #18cfab;
}
.header_link:hover{
    color: #18cfab;
    margin-bottom: -43px;
    padding-bottom: 41px;
    border-bottom: 2px solid #18cfab;
}