.weAre{
    background-image: url("../img/Sections_background/section-1(header).jpg");
    height: 1014px;
    background-attachment: fixed;
/*    margin-top: -110px;*/
/*    z-index: 1;*/
}
.weAre .container{
    height: 100%;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.header .header_container-flex{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.weAre_box{
    text-align: center;
}
.weAre_wrap-box{
    text-align: center;
}
.weAre_subtitle{
    font-size: 35px;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 40px;
}
.weAre_btn_box{
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
}
.weAre_title{
    font-size: 79px;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom:40px;
}
.header_subtitle_button{
    font-size: 35px;
    font-family: 'Montserrat', sans-serif;
    color: #9f9e9e;
    margin-bottom:140px ;
}
.btn_link{
    width: 165px;
    text-decoration: none;
    background: #18cfab;
    margin-right: 28px;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    padding: 25px;
    border-radius: 3px;
    font-weight: bold;
    -webkit-box-shadow: 0px -4px 0px #fff;
    box-shadow: 0px -4px 0px #fff;
}
.blue{
    background-color: #00cbf6;
}