body, html {
    width: 100%;
    height: 100%;
}
*{
    box-sizing: border-box;
}
body {
    font-size: 12px;
    line-height: 1.2;
    -webkit-font-smoothing: antialiased;
    font-family: Arial, Helvetica, sans-serif;

}

.wrapper {
    overflow: hidden;
    max-width: 1920px;
    width: 100%;
    margin: 0 auto;
}
a{
    text-decoration: none;
}
.container {
    width: 1141px;
    margin:  0 auto;
}
.green{
    color: #18cfab;
}
.flex{
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}
.img_fluid{
    display: block;
    max-width: 100%;
    max-height:100%;
    margin: 0 auto;
}
.section_title{
    text-align: center;
    font-size: 36px;
    font-family: 'Montserrat', sans-serif;
    color: #4e4e4e;
    margin-bottom:70px;
}
.section_title:after{
    content: "";
    display: block;
    height: 1px;
    background-color: #18cfab;
    width: 76px;
    margin: 45px auto;
    -webkit-box-shadow: 12px 3px 0px #18cfab;
    box-shadow: 12px 3px 0px #18cfab;
}

