.justBuy{
    background-image:url("../img/Sections_background/section-2.jpg");
    background-size:cover ;
    background-position: center;
    background-attachment: fixed;
    height: 478px;
    position: relative;
}
.justBuy:before{
    content: "";
    background-color: rgba(23, 22, 22, 0.79);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
}
.justBuy .container{
    height: 100%;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;

}
.justBuy_content{
    z-index: 1;
    text-align: center;

}
.justBuy_title{
    color: #18cfab;
    font-size: 40px;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 35px;
}
.justBuy_subtitle{
    font-size: 16px;
    color: #d8d8d8;
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 100px;
}
