.slider{
    margin: 0 auto;
}
.slide-img{
    width: 100%;
    height: 100%;
    display: block;
}
.slide_item{
    display: none;
}
.slide_item.active{
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;

}
.sliderTop_img-box{
    width: 150px;
    height: 150px;
    border: 3px solid #18cfab;
    overflow: hidden;
    border-radius: 50%;
    -webkit-box-shadow: 1px 1px 11px #18cfab;
    box-shadow: 0px 0px 0px 11px rgba(24, 207, 171, 0.33);
    margin-bottom: 20px;
}
.peopleSey_content{
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    line-height: 1.5;
    color: #a3a7a8;
    margin-bottom:45px;
    width: 1000px;
    -webkit-animation-name: content_people;
    animation-name: content_people;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-timing-function: ease;
    animation-timing-function: ease;
}
@-webkit-keyframes content_people{
    0%{
        margin-left: 100px;
        opacity: 0;
    }
    100%{
        margin-left: 0;
        opacity: 1;
    }
}
@keyframes content_people{
    0%{
        margin-left: 100px;
        opacity: 0;
    }
    100%{
        margin-left: 0;
        opacity: 1;
    }
}
.slider-title{
    text-transform: uppercase;
    color: #18cfab;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    margin-bottom: 15px;
    font-size: 16px;
}
.slide_title-box{
    text-align: center;
    width: 100%;
}
.slider_subtitle{
    font-family: 'Montserrat', sans-serif;
    color: #bbbbbb;
    font-size: 16px;
    margin-bottom: 30px;
}
.slideShow_top{
    margin-bottom: 50px;
}
.slide_itemLink{
    cursor: pointer;
     width: 70px;
    height: 60px;
    overflow: hidden;
    border-radius: 50%;
    -webkit-box-shadow: 0px 0px 0pc 3px #18cfab;
    box-shadow: 0px 0px 0px 3px rgba(24, 207, 171, 0.33);
     display: inline-block;
    margin-right:35px;
}
.slide_itemLink.active{
    border: 2px solid #18cfab;
    position: relative;
    bottom: 17px;
    -webkit-animation-name: fadeBottom;
    animation-name: fadeBottom;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-timing-function: ease;
    animation-timing-function: ease;

}
.slider-bottom-box{
    width: 500px;
    margin: 0 auto;
}
.slideShow_bottom{
    text-align: center;
    position: relative;
    max-width: 500px;
    height: 100px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0 20px;
}
.box_mask{
    max-width: 345px;
    height: 100px;
    overflow: hidden;
    position: relative;
    margin: 0 auto;
}
.slider_line{
    padding-top: 20px;
    position: absolute;
    width: 1000px;
    left: -85px;
    height: 100px;
}
.arrow{
    position: absolute;
    background-color: #ce0707;
    background-color: transparent;
    color: #bbbbbb;
    font-size: 20px;
    border: 1px solid #bbbbbb;
    padding: 5px 8px 3px 8px;
    margin-top: -10px;
    cursor: pointer;
    font-weight: bold;
    -webkit-transition: 0.6s ease;
    transition: 0.6s ease;
}
.arrow.active{
    background-color: #18cfab;
    border: 1px solid #18cfab;
    color: #fff;
}
.prev{
    left:0;
    top:50%;
}
.next{
    right:0;
    top: 50%;
}
.fadeBottom{
    -webkit-animation-name: fadeBotton;
    animation-name: fadeBotton;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-timing-function: ease;
    animation-timing-function: ease;
}
@-webkit-keyframes fadeBottom {
    0%{
        bottom: 0;
    }
    100%{
        bottom: 17px;
    }
}
@-webkit-keyframes fadeBotton {
    0%{
        bottom: 0;
    }
    100%{
        bottom: 17px;
    }
}
.fade{
    -webkit-animation-name: fade;
    animation-name: fade;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-timing-function: ease;
    animation-timing-function: ease;
}
@-webkit-keyframes fade {
    0%{
        opacity: 0;
        margin-right: -100px;
    }
    100%{
        opacity: 1;
        margin-right: 0;
    }
}
@keyframes fade {
    0%{
        opacity: 0;
        margin-right: -100px;
    }
    100%{
        opacity: 1;
        margin-right: 0;
    }
}
