.services .container{
    margin-bottom: 100px;
}
.services_tabs{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
}
.services_item-tabs{
    color: #717171;
     font-family: 'Montserrat', sans-serif;
    border: 1px solid #eeeeef;
    text-align: center;
    padding: 25px 0;
    margin-bottom: 24px;
    background-color: #f8fcfe;
    -webkit-box-shadow: 1px 1px 1px #eeeeef;
    box-shadow: 1px 1px 0px rgba(238, 238, 239, 0.37);
    position: relative;
    cursor: pointer;
}
.services_item-tabs:nth-child(odd){
    border-left: none;
    border-right: none;
}
.services_item-tabs.active{
    background-color: #18cfab;
    color: #fff;
}
.services_item-tabs.active:after{
    content: "";
    width: 0;
    height: 0;
    border: 15px solid transparent;
    border-top: 15px solid #18cfab;
    display: inline-block;
    margin-top: -1px;
    position: absolute;
    top: 65px;
    left: 50%;
    margin-left: -15px;
}
.services_articles-item{
    width: 100%;
    display: none;
}
.services_articles-item.active{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.servises_img-box{
    width: 200px;
    height: 140px;
    margin-right: 20px;
}
.paragraph_box{
    width: 75%;
}
.servises_img.img_fluid{
    display: inline;
    border: 1px solid #b2b4a8;
}
.services_article{
    font-size: 14px;
     font-family: 'Montserrat', sans-serif;
    line-height: 2;
    color: #717171
    }